<template>
    <div class="container">
       <div class="main__cars" v-if="todos">
        <div class="main__cars-item" v-for="todo in todos" :key="todo.id">
            <p>ID: {{ todo.id }}</p>
            <p>Title: {{ todo.title }}</p>
            <p>completed: {{ todo.completed }}</p>
        </div>
       </div>
       <div v-else>
        Идет загрузка...
       </div>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                todos: null
            }
        },
        created() {
            this.getTodos()
        },
        methods: {
            async getTodos() {
                let res = await fetch('https://jsonplaceholder.typicode.com/todos')
                let data = await res.json()
                this.todos = data
            }
        },
    }
</script>